/* GLOBAL */
body { margin: 0; background-color: #121212; color: white; font-family: 'Arial', sans-serif; -webkit-font-smoothing: antialiased; }
.App.mobile-layout { display: flex; flex-direction: column; min-height: 100vh; }
header { padding: 20px; text-align: center; letter-spacing: 3px; font-weight: bold; border-bottom: 1px solid #333; }
h3 { margin: 0; color: #fff; }
.content { flex: 1; padding: 20px; display: flex; flex-direction: column; justify-content: center; }

/* HOME MENU */
.home-menu { display: flex; flex-direction: column; gap: 20px; }
.big-choice-btn {
  padding: 30px; font-size: 1.5rem; font-weight: bold; color: white; border: none; border-radius: 10px; cursor: pointer; text-transform: uppercase;
}
.create { background-color: #e94560; }
.join { background-color: #333; border: 2px solid #555; }

/* FORMS & INPUTS */
.menu-grid { display: flex; flex-direction: column; gap: 15px; text-align: center; }
.input-field {
  padding: 15px; background: #222; border: 1px solid #444; color: white; 
  font-size: 1.2rem; border-radius: 8px; text-align: center; width: 100%; box-sizing: border-box;
}
.label { color: #888; font-size: 0.9rem; margin-top: 10px; }

/* BOUTONS JEUX (TEXTE SEULEMENT) */
.game-btn { width: 100%; padding: 20px; border-radius: 8px; font-size: 1.5rem; font-weight: bold; color: white; border: none; cursor: pointer; margin-bottom: 10px; }
.picolo-btn { background-color: #c0392b; }
.undercover-btn { background-color: #27ae60; }
.back-btn { background: transparent; color: #888; padding: 10px; margin-top: 10px; border: none; text-decoration: underline; font-size: 1rem; }
.join-confirm-btn { background-color: #fff; color: #000; padding: 15px; font-weight: bold; border-radius: 8px; border: none; font-size: 1.2rem; }

/* LOBBY */
.big-code { font-size: 4rem; color: #e94560; font-weight: bold; letter-spacing: 5px; display: block; margin: 10px 0; }
.player-chip { display: inline-block; background: #333; padding: 8px 15px; border-radius: 4px; margin: 5px; font-weight: bold; border: 1px solid #444; }
.start-btn { width: 100%; padding: 20px; background: #e94560; color: white; font-size: 1.3rem; font-weight: bold; border: none; border-radius: 50px; margin-top: 30px; box-shadow: 0 4px 20px rgba(233, 69, 96, 0.5); }
.pulse { animation: pulse 1.5s infinite; opacity: 0.6; text-align: center; margin-top: 20px; }

/* GAME ELEMENTS */
.secret-card {
  background: #1e1e1e; border: 2px solid #333; border-radius: 10px;
  height: 350px; display: flex; align-items: center; justify-content: center;
  cursor: pointer; user-select: none; margin-top: 10px;
}
.secret-card.revealed { background: #fff; color: #000; border: none; }
.lock-icon { font-size: 3rem; margin-bottom: 10px; display: block; }
.action-btn { position: fixed; bottom: 30px; left: 10%; width: 80%; padding: 15px; background: #e94560; color: white; border-radius: 30px; font-weight: bold; border: none; font-size: 1.2rem; box-shadow: 0 5px 15px rgba(0,0,0,0.5); }

/* VOTE */
.vote-list { display: flex; flex-direction: column; gap: 10px; margin-top: 20px; }
.vote-btn { background: #2d3436; color: white; border: 1px solid #444; padding: 20px; font-size: 1.2rem; border-radius: 8px; text-align: center; }

/* RESULT */
.result-screen { text-align: center; margin-top: 40px; }
.eliminated-name { font-size: 3rem; color: #e74c3c; text-transform: uppercase; margin: 10px 0; }
.role-badge { background: #fff; color: #000; padding: 10px 20px; border-radius: 4px; font-weight: bold; font-size: 1.5rem; display: inline-block; margin-top: 10px; }

@keyframes pulse { 0% { opacity: 0.5; } 50% { opacity: 1; } 100% { opacity: 0.5; } }